import { useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  Link,
  Route,
  withRouter,
  Switch,
} from "react-router-dom";
import styled from "styled-components";
import DetailProduction from "./Tabs/DetailProduction";
import DetailVideos from "./Tabs/DetailVideos";

const TabContainer = styled.ul`
  display: flex;
  float: left;
  /* margin-top: 10px; */
  height: 30px;
  width: 200px;
  margin-bottom: 5px;
`;

const Tabs = styled.button`
  all: unset;
  width: 80px;
  font-size: 12px;
  text-align: center;
  color: white;
  text-shadow: 0.5px 0.5px 10px black;
  height: 20px;
  z-index: 100;
  border: ${(props) => (props.color ? "2px solid #F58E85" : "2px solid wheat")};
  background: ${(props) => (props.color ? "rgb(250,65,32,0.5)" : "none")};
  box-shadow: ${(props) =>
    props.color ? "0px 0px 2px red" : "0px 0px 10px black"};
`;

const Tabs2 = styled.button`
all: unset;
width: 100%;
color: white;
text-align: center;
border:2px solid wheat;
z-index: 100;
height:25px;
margin-bottom:20px;
margin-top:10px;
white-space:nowrap;
`

const SLink = styled(Link)`
  margin-right: 10px;
`;

const DetailRoute = withRouter((props) => {
  const {
    match: { url },
    videos: { results: videos },
    production_countries,
    production_companies,
    imdb_id
  } = props;

  const [selected, changeselected] = useState();

  useEffect(() => {
    console.log(`DetailRoute: isMovie: ${props.isMovie}`);
  }, [selected]);

  return (
    <Router>
      <TabContainer>
        <SLink to={`${url}/videos`}>
          <Tabs onClick={() => changeselected(0)}  color={selected === 0}>Videos</Tabs>
        </SLink>
        <SLink to={`${url}/production`}>
          <Tabs onClick={() => changeselected(1)}  color={selected === 1}>Production</Tabs>
        </SLink >
                
      </TabContainer>
      <Switch>
        <Route
          path={`${url}/videos`}
          exact
          render={() => <DetailVideos props={{ videos: videos }} />}
        />
        {/* Routeì— custom propì„ ë„˜ê¸°ëŠ” ë°©ë²•! ì°¸ê³ í•˜ì.. ì—¬ê¸°ì„œ componentì— renderë¥¼ ì“°ë©´ ì¡°ê¸ˆë” ì„±ëŠ¥ í–¥ìƒì´ ìˆë‹¤ê³  í•œë‹¤. ì™œëƒí•˜ë©´ ì»´í¬ë„ŒíŠ¸ì˜ propì´ ë°”ë€”ë•Œë§ˆë‹¤ ì¬ìƒì„±ì´ ì¼ì–´ë‚˜ê¸° ë•Œë¬¸ì´ë‹¤!*/}
        <Route
          path={`${url}/production`}
          render={() => (
            <DetailProduction
              props={{
                production_companies: production_companies,
                production_countries: production_countries,
              }}
            />
          )}
        />
      </Switch>
      {imdb_id ? <a href={`https://www.imdb.com/title/${imdb_id}`} target={'_blank'}><Tabs2>More Information.... Link to IMDb</Tabs2></a> : <Tabs2>There is no database in IMDb</Tabs2>}
    </Router>
  );
});

export default DetailRoute;
