import React, { useEffect, useState } from "react";
import { withRouter } from "react-router";
import styled from "styled-components";
import Loader from "../../../Components/Loader";

const NaviBar = styled.div`
  display: flex;
  justify-content: space-between;
  /* margin: 10px 0px; */
  margin-bottom:0px;
`;

const NaviButton = styled.button`
  background: unset;
  color: white;
  cursor: pointer;
`;

const DotContainer = styled.div`
  justify-content: center;
  align-items: center;
  display: flex;
`;

const Dots = styled.span`
  font-size: 15px;
  line-height: 20px;
  margin: 5px;
`;

const IframeContainer = styled.div`
position: relative;
  width: 100%;
  padding-top: 56.25%; /* 4:3 Aspect Ratio */
  /* ìœ„ ì»¨í…Œì´ë„ˆë¥¼ ì´ìš©í•œ ì½”ë“œëŠ” aspect-ratioê°€ í†µí•˜ì§€ ì•ŠëŠ” ë¸Œë¼ìš°ì €ê°€ ì•„ì§ ë§ê¸° ë•Œë¬¸ì— ì´ë¥¼ ë‹¬ì„±í•˜ê²Œ í•´ì£¼ëŠ” ì¼ì¢…ì˜ í•µì´ë‹¤. */
  /*https://web.dev/aspect-ratio/ ì°¸ê³ .  */
`

const Iframe = styled.iframe`
position:absolute;
top:0;
width:100%;
height:100%;
  /* width: 100%; */
  /* aspect-ratio: 1.68/1; */
  /* margin-bottom: 50px; */
`;

const DetailVideos = withRouter((props) => {
  const [index, setIndex] = useState(0);
  const [error, setError] = useState();
  const [loading, setLoading] = useState(true);
  const [dots, setDots] = useState([]);

  const {
    props: { videos },
  } = props;
  const indexUp = () => {
    if (videos.length === 1) return;
    else if (videos.length - 1 === index) setIndex(0);
    else setIndex(index + 1);
    setLoading(true);
  };

  const indexDown = () => {
    if (videos.length === 1) return;
    if (index === 0) setIndex(videos.length - 1);
    else setIndex(index - 1);
    setLoading(true);
  };

  useEffect(() => {
    console.log(props);
    const makeingdots = [];
    for (let i = 0; i < videos.length; i++) {
      if (i === index) makeingdots.push("â—†");
      else makeingdots.push("â—‹");
      setDots(makeingdots);
    }
  }, [index]);

  useEffect(() => {
    setLoading(false);
  }, [index]);

  return videos.length === 0 ? (
    "No videos available"
  ) : loading ? (
    <Loader />
  ) : (
    <>
    <IframeContainer>
      <Iframe
        className={"Video"}
        src={`https://www.youtube.com/embed/${videos[index].key}`}
        title="YouTube video player"
        frameBorder="0"
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
        allowFullScreen
      ></Iframe>
      </IframeContainer> 
      <NaviBar>
        <NaviButton key="left" onClick={indexDown}>â«·</NaviButton>
        <DotContainer>
          {dots.map((dot, index) => (
            <Dots key={videos[index].key}> {dot} </Dots>
          ))}
        </DotContainer>
        <NaviButton key="right" onClick={indexUp}>â«¸</NaviButton>
      </NaviBar>
    </>
  );
});

export default DetailVideos;
